import React, { useEffect } from 'react'
import styled from 'styled-components'
import Image from 'gatsby-image'

import { navigate } from '@reach/router'

import { desktopVw } from '../../styles/utils'
import { getCurrentUser, isLoggedIn } from '../../utils/auth'

import SidebarNav from './SidebarNav'

const StyledAccountWrapper = styled.div``

const Wrapper = styled.div`
  position: relative;
  height: 80px;
  margin-bottom: -30px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: -${props => desktopVw(props.theme.header.desktop.height + 2)};
    height: ${desktopVw(77)};
    display: flex;
    justify-content: center;
    align-items: center;
  }
`

const Container = styled.div`
  position: relative;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    padding: ${desktopVw(61)} ${desktopVw(80)} ${desktopVw(120)}
      ${desktopVw(80)};
  }
`

const WelcomeText = styled.div`
  position: absolute;
  left: 20px;
  bottom: 32px;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 30px;
  font-family: ${props => props.theme.fonts.heading};
  word-break: break-word;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const ContentWrapper = styled.div`
  width: 100%;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
  }
`

const AccountWrapper = ({ children, heroImage, menu, contact, activeSlug }) => {
  useEffect(() => {
    if (!isLoggedIn()) {
      navigate(`/log-in?redirecturi=${window.location.href}`)
    }
  }, [])

  const user = getCurrentUser()

  return (
    <StyledAccountWrapper>
      <Wrapper>
        <WelcomeText>{`Hi ${user.name}!`}</WelcomeText>
      </Wrapper>
      <Container>
        <SidebarNav
          menuList={menu}
          contact={contact}
          activeSlug={activeSlug}
          user={user}
        />
        <ContentWrapper>{children}</ContentWrapper>
      </Container>
    </StyledAccountWrapper>
  )
}

export default AccountWrapper
