import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { graphql, Link } from 'gatsby'
import Image from 'gatsby-image'
import dayjs from 'dayjs'
import PageHeader from '../../components/Account/PageHeader'
import AccountWrapper from '../../components/Account/AccountWrapper'
import { desktopVw, mobileVw } from '../../styles/utils'
import Button from '../../components/Button'
import useDetectDevices from '../../hooks/useDetectDevices'
import {
  fetchStoreCreditBalance,
  fetchStoreCreditTransactions,
} from '../../utils/api'

const StyledButton = styled(Button)`
  text-transform: uppercase;
  font-size: 9px;
  padding: 10px 40px;
  width: 100%;
  color: ${({ theme }) => theme.colors.secondary};
  background: ${({ theme }) => theme.colors.primary};
  font-family: ${({ theme }) => theme.fonts.heading};
`

const Container = styled.div`
  margin: 0 21px 33px 21px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: ${desktopVw(40)} ${desktopVw(120)} ${desktopVw(76)} 0;
  }
`

const StoreCreditImageWrapper = styled.div`
  position: relative;
  height: ${mobileVw(225)};
  margin-bottom: 33px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: ${desktopVw(225)};
    width: ${desktopVw(333)};
  }
`

const Title = styled.p`
  font-size: 20px;
  line-height: 25px;
  font-weight: 500;
  padding: 19px 21px;
`

const SubTitle = styled.p`
  font-size: 20px;
  font-weight: 500;
  font-family: ${({ theme }) => theme.fonts.heading};
`

const ActivityContainer = styled.div`
  margin: 29px 0 24px;
  border: 0.5px solid #c4c4c4;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: ${desktopVw(37)};
  }
`

const DesktopScrollableContainer = styled.div`
  max-height: ${desktopVw(478)};
  overflow-y: scroll;
`

const DetailContainer = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 15px 0;
  margin: 0 24px;
  border-bottom: 1px solid #e0e0e0;
  &:last-child {
    border-bottom: none;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(8)} 0;
    margin: 0 ${desktopVw(24)};
  }
`

const TableHeaderDesktop = styled.p`
  font-size: ${desktopVw(10)};
  font-weight: 500;
  width: ${props => desktopVw(props.width)};
`

const Value = styled.p`
  position: absolute;
  font-size: 20px;
  font-weight: 500;
  line-height: 16px;
  padding: 30px 21px;
  bottom: 0;
  font-family: ${({ theme }) => theme.fonts.heading};
`

const TransactionDetail = styled.p`
  line-height: 15px;
  font-size: 11px;
  color: ${({ theme }) => theme.colors.darkGrey};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(10)};
    width: ${desktopVw(200)};
  }
`

const TransactionValue = styled.p`
  font-size: 13px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(10)};
    color: ${({ theme }) => theme.colors.darkGrey};
    width: ${desktopVw(100)};
    text-align: right;
  }
`

const TransactionTime = styled.p`
  line-height: 14px;
  font-size: 11px;
  color: ${({ theme }) => theme.colors.darkGrey};
  text-transform: uppercase;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(10)};
    width: ${desktopVw(100)};
  }
`

function getOrderLink(transaction) {
  return (
    <Link
      to='/account/my-orders-returns'
      state={{ orderNumber: transaction?.detail?.orderNumber }}
      style={{ textDecoration: 'underline' }}
    >
      #{transaction?.detail?.orderNumber}
    </Link>
  )
}

function getPaymentText(transaction) {
  return (
    <>
      {transaction?.type === 'CREDIT' ? 'Refund' : 'Payment'} towards
      <br />
      order {getOrderLink(transaction)}
    </>
  )
}

function getPaymentTextDesktop(transaction) {
  return (
    <>
      {transaction?.type === 'CREDIT' ? 'Refund' : 'Payment'} towards order{' '}
      {getOrderLink(transaction)}
    </>
  )
}

function getTransactionValue(transaction) {
  return (
    <>
      {transaction?.type === 'CREDIT' ? '+' : '-'}$
      {parseFloat(transaction?.amount).toFixed(2)}
    </>
  )
}

function getTransactionTime(transaction) {
  return dayjs(transaction?.transactionTime).format('MMM DD, YYYY')
}

function MobileTransactions({ mobileTransactions }) {
  return mobileTransactions.map(transaction => (
    <DetailContainer key={transaction?.id}>
      <TransactionDetail>{getPaymentText(transaction)}</TransactionDetail>
      <div style={{ textAlign: 'right' }}>
        <TransactionValue>{getTransactionValue(transaction)}</TransactionValue>
        <TransactionTime>{getTransactionTime(transaction)}</TransactionTime>
      </div>
    </DetailContainer>
  ))
}

function DesktopTransactions({ transactions }) {
  return (
    <>
      <DetailContainer style={{ marginRight: desktopVw(39) }}>
        <TableHeaderDesktop width={100}>DATE</TableHeaderDesktop>
        <TableHeaderDesktop width={200}>DESCRIPTION</TableHeaderDesktop>
        <TableHeaderDesktop width={100} style={{ textAlign: 'right' }}>
          AMOUNT
        </TableHeaderDesktop>
      </DetailContainer>
      <DesktopScrollableContainer>
        {transactions.map(transaction => (
          <DetailContainer key={transaction?.id}>
            <TransactionTime>{getTransactionTime(transaction)}</TransactionTime>
            <TransactionDetail>
              {getPaymentTextDesktop(transaction)}
            </TransactionDetail>
            <TransactionValue>
              {getTransactionValue(transaction)}
            </TransactionValue>
          </DetailContainer>
        ))}
      </DesktopScrollableContainer>
    </>
  )
}

function MyStoreCredit({
  data: {
    contentfulAccountPage: {
      contact,
      hero,
      menuList,
      slug,
      text,
      title,
      featuredImage,
    },
  },
}) {
  const [balance, setBalance] = useState(0)
  const [transactions, setTransactions] = useState([])
  const [mobileTransactions, setMobileTransactions] = useState([])

  const { devices } = useDetectDevices()

  useEffect(() => {
    fetchStoreCreditTransactions().then(setTransactions)
    fetchStoreCreditBalance().then(setBalance)
  }, [])

  useEffect(() => {
    setMobileTransactions(transactions.slice(0, 7))
  }, [transactions])

  return (
    <AccountWrapper
      heroImage={hero}
      menu={menuList}
      contact={contact}
      activeSlug={slug}
    >
      <PageHeader title={title?.title} text={text?.text} />
      <Container>
        <StoreCreditImageWrapper>
          <Image
            fluid={featuredImage?.fluid}
            style={{
              position: 'absolute',
              left: '0',
              right: '0',
              bottom: '0',
              top: '0',
              zIndex: '-1',
            }}
            alt={featuredImage?.description || featuredImage?.title}
          />
          <Title>
            Store Credit
            <br /> Balance
          </Title>
          <Value>${parseFloat(balance).toFixed(2)}</Value>
        </StoreCreditImageWrapper>
        {devices.mobile && <SubTitle>Activity</SubTitle>}
        {(transactions ?? []).length > 0 && (
          <ActivityContainer>
            {devices.mobile && (
              <MobileTransactions mobileTransactions={mobileTransactions} />
            )}
            {devices.desktop && (
              <DesktopTransactions transactions={transactions} />
            )}
          </ActivityContainer>
        )}
        {transactions.length !== mobileTransactions.length && devices.mobile && (
          <StyledButton
            onClick={() => {
              setMobileTransactions(transactions)
            }}
          >
            See Previous Store Credit Activity
          </StyledButton>
        )}
      </Container>
    </AccountWrapper>
  )
}

export default MyStoreCredit

export const MyStoreCreditQuery = graphql`
  query MyStoreCredit($id: String!) {
    contentfulAccountPage(id: { eq: $id }) {
      id
      slug
      title {
        title
      }
      pageType
      hero {
        fluid(maxWidth: 3840) {
          ...GatsbyContentfulFluid_withWebp
        }
      }
      menuList {
        ...Link
      }
      featuredImage {
        fluid(maxWidth: 640) {
          ...GatsbyContentfulFluid_withWebp
        }
      }
      text {
        text
      }
      contact {
        json
      }
    }
  }
`
