import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../styles/utils'

const StyledPageHeader = styled.div`
  padding: 0 20px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0;
    margin-bottom: ${desktopVw(45)};
  }
`

const Title = styled.h1`
  display: none;
  margin: 0 0 8px;
  font-size: 40px;
  line-height: 45px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
    white-space: pre-wrap;
    margin: 0 0 ${desktopVw(17)};
    border-bottom: none;
    font-family: ${props => props.theme.fonts.heading};
    font-size: ${desktopVw(40)};
    line-height: ${desktopVw(45)};
    font-weight: 500;
    padding: 0;
  }
`
const Text = styled.p`
  margin: 0 0 34px;
  font-size: 12px;
  line-height: 20px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0;
    font-size: ${desktopVw(12)};
    font-weight: normal;
    line-height: ${desktopVw(20)};
    width: ${desktopVw(480)};
  }
`

const PageHeader = ({ className, title, text }) => {
  return (
    <StyledPageHeader className={className}>
      <Title>{title}</Title>
      <Text>{text}</Text>
    </StyledPageHeader>
  )
}

export default PageHeader
