import React from 'react'
import styled from 'styled-components'
import { navigate } from '@reach/router'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'

// Styles
import { desktopVw } from '../../styles/utils'

// Utils
import { getCookie, getLinkPath, logout } from '../../utils'

// Components
import TextButton from '../TextButton'

import DropdownIcon from '../../assets/dropdown.svg'
import { useSignoutUser } from '../../hooks/useSignoutUser'
import { ANALYTIC_COOKIES } from '../../utils/constants'

const StyledSidebarNav = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(80)};
    width: ${desktopVw(235)};
  }
`
const SelectWrapper = styled.div`
  position: relative;
  margin: 0 0 30px;
  box-sizing: border-box;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const MobileSelect = styled.select`
  font-size: 20px;
  padding: 20px;
  background: none;
  width: 100%;
  -webkit-appearance: none;
  border-radius: 0;
  border: none;
  border-bottom: 1px solid ${props => props.theme.colors.greyLighter};
`

const Dropdown = styled(DropdownIcon)`
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 20px;
  pointer-events: none;
`

const paddingTop = 61

const Sticky = styled.div`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
    position: sticky;
    top: ${props => desktopVw(props.theme.header.desktop.height + paddingTop)};
  }
`

const WelcomeText = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: relative;
    text-transform: uppercase;
    font-weight: 500;
    font-family: ${props => props.theme.fonts.heading};
    font-size: ${desktopVw(12)};
    line-height: ${desktopVw(15)};
    margin-bottom: ${desktopVw(22)};
    word-break: break-word;
  }
`

const NavWrapper = styled.div`
  display: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: 100%;
    display: block;
  }
`

const Nav = styled.nav`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    flex-direction: column;
    width: ${desktopVw(170)};
    margin-right: ${desktopVw(160)};
    padding-bottom: ${desktopVw(15)};
  }
`

const NavItem = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
    margin-bottom: ${desktopVw(10)};
  }
`

const ContactDetails = styled.div`
  display: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
    line-height: ${desktopVw(15)};
    text-transform: uppercase;
    border-top: 1px solid ${props => props.theme.colors.silver};
    font-size: ${desktopVw(10)};
    padding-top: ${desktopVw(15)};
    margin-bottom: ${desktopVw(14)};
    width: ${desktopVw(138)};
  }
`

const Logout = styled(TextButton)`
  font-size: 10px;
  font-family: ${props => props.theme.fonts.primary};
  color: ${props => props.theme.colors.greyLight};
  text-transform: uppercase;
  cursor: pointer;
  background-image: ${props =>
    `linear-gradient(${props.theme.colors.greyLight}, ${props.theme.colors.greyLight});`};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(10)};
  }
`

const SidebarNav = ({ menuList, contact, activeSlug, user }) => {
  const { signOutUser } = useSignoutUser()

  const handleChange = e => {
    const { value } = e.target

    if (value === 'logout') {
      signOutUser()
    } else navigate(value)
  }

  return (
    <StyledSidebarNav>
      <SelectWrapper>
        <MobileSelect onChange={e => handleChange(e)} value={`/${activeSlug}`}>
          {menuList.map(menuItem => (
            <option key={menuItem.id} value={getLinkPath(menuItem)}>
              {menuItem.text}
            </option>
          ))}
          <option value='logout'>Logout</option>
        </MobileSelect>
        <Dropdown />
      </SelectWrapper>
      <Sticky>
        <WelcomeText>
          Hi <br /> {`${user.name}!`}
        </WelcomeText>
        <NavWrapper>
          <Nav>
            {menuList.map(menuItem => (
              <NavItem key={menuItem.id}>
                <TextButton
                  to={getLinkPath(menuItem)}
                  active={`/${activeSlug}` === getLinkPath(menuItem)}
                >
                  {menuItem.text}
                </TextButton>
              </NavItem>
            ))}
          </Nav>
          <ContactDetails>
            {documentToReactComponents(contact.json)}
          </ContactDetails>
          <Logout
            onClick={signOutUser}
            trackParams={{
              trackInternal: true,
              category: 'login',
              action: 'account_logout',
              context: {
                login_email: user.email,
                lb_user_id: getCookie(ANALYTIC_COOKIES.LB_USER_ID),
              },
            }}
          >
            Logout
          </Logout>
        </NavWrapper>
      </Sticky>
    </StyledSidebarNav>
  )
}

export default SidebarNav
